.navbarColor {
  background: linear-gradient(
      90deg,
      /* rgb(109, 42, 30) 10%, */
      rgb(32, 32, 73) 25%,
      rgb(15, 3, 3) 62%,
      rgba(208, 227, 231, 0.671) 100%
    )
    no-repeat;
}

.profilePictureAbout {
  border-radius:50% !important;
}

.li-list {
  font-size: 1.4em;
  color: white;
  margin-left: 10x !important;
}

a.li-list:hover {
  color: #14959a !important;
}

.home {
  background: url("./img/pexels-valiphotos-589842.jpg") no-repeat center center;
  background-size: cover;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.header-name {
  font-size: 6em;
  /* color: #14959a; */
  text-shadow: 5px 0px #061414;
}

.header-description {
  font-size: 1.5em;
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.8);
}

.home .scrolldown a {
  position: absolute;
  font-size: 48px;
  color: white;
  bottom: 40px;
  left: 50%;
  margin-left: -30px;
  display: block;
  line-height: 80px;
  border-radius: 100%;
}

.home .scrolldown a:hover {
  color: #14959a !important;
}

/* Fixed/sticky icon bar (vertically aligned 50% from the top of the screen) */
.icon-bar {
  position: fixed;
  top: 50%;
  z-index: 10;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

/* Style the icon bar links */
.icon-bar a {
  display: block;
  text-align: center;
  padding: 16px;
  transition: all 0.3s ease;
  color: white;
  font-size: 15px;
}

/* Style the social media icons  */
.icon-bar a:hover {
  background-color: #ddd;
}

.email {
  background: #55acee;
  color: white;
}

.phone {
  background: #719bac;
  color: white;
}

.linkedin {
  background: #007bb5;
  color: white;
}

.github {
  background: #814f8f;
  color: white;
}

#portfolio {
  background: url("./img/pexels-michael-morse-1481349.jpg") no-repeat center center fixed;
  background-size: cover;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.card-style:hover {
  transform: scale(1.02);
}

#aboutme {
  background-color: #68233a;
  background-image: linear-gradient(238deg, #a55f73 20%, #1c3d57 80%);
}
.header-aboutme {
  font-weight: bold;
  background: -webkit-linear-gradient(#03d4f0, #003cff);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

.header-Projects {
  font-weight: bold;
  background: -webkit-linear-gradient(#03d4f0, #003cff);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

.card-title {
  background: -webkit-linear-gradient(#2213cc, #06c1da);
  background-clip: border-box;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}

.aboutme-description {
  margin: 20px 10px 0 10px;
  text-align: justify;
  font-size: 1.3em;
  color: black;
  
}

.line {
  background: linear-gradient(to right, #281cae, #029bb0);
  height: 2px;
}

.color-adjuster {
  background-color: #ffdee9;
  background-image: linear-gradient(238deg, #e1d3d7 20%, #769fbf 80%);
  padding: 40px;
}

.techstack-container {
  padding: 80px 20px;
}

/* Footer icons */
.icons {
  text-shadow: 0px 1px 3px rgba(0, 0, 0, 0.8);
}

.icons:hover {
  transform: scale(1.5);
  color: white !important;
}

.icons-card:hover {
  transform: scale(1.3) !important;
}

.footer {
  position: relative;
  background: linear-gradient(
      90deg,
      rgba(25, 25, 121, 1) 10%,
      rgb(15, 15, 52) 25%,
      rgb(11, 3, 3) 62%,
      rgba(13, 148, 182, 0.671) 100%
    )
    no-repeat;
}

#go-top {
  top: -35px;
  left: 50%;
  display: block;
  line-height: 80px;
  border-radius: 100%;
}

#go-top a {
  position: relative;
  font-size: 50px;
  color: white;
  bottom: 60px;
}

#go-top a:hover {
  color: #0f9095;
}

@media screen and (max-width: 575.98px) {
  .header-name {
    font-size: 1.5em;
    text-shadow: 2px 0px #14959a;
  }

  .header-description {
    font-size: 1.02em;
  }

  .aboutme-description {
    font-size: 18px;
  }

  ul.navbar.nav.justify-content-start {
    padding-left: 0px;
    padding-right: 0px;
  }

  a.nav-link {
    padding-left: 5px;
    font: 15px "opensans-bold", sans-serif;
    padding-right: 12px;
  }

  .home .scrolldown a {
    font-size: 30px;
    margin-left: -22px;
  }

  .icons {
    font-size: 25px !important;
  }
  .icon-bar a {
    font-size: 10px;
    padding: 10px;
  }
}

@media (max-width: 768px) and (min-width: 575.98px) {
  .header-name {
    font-size: 3.5em;
    text-shadow: 4px 0px #14959a;
  }

  .header-description {
    font-size: 1em;
  }

  .profile-img {
    width: 200px;
    margin: 0 auto;
    display: block;
    clip-path: circle(100px at center);
  }
}

@media (max-width: 1024px) and (min-width: 991.98px) {
  .header-name {
    font-size: 5em;
    text-shadow: 5px 2px #14959a;
  }

  .header-description {
    font-size: 2.2em;
  }

  a.icons {
    font-size: 35px;
  }

  .profile-img {
    width: 200px;
    margin: 0 auto;
    display: block;
    clip-path: circle(100px at center);
  }
  .home .scrolldown a {
    font-size: 48px;
  }

  .li-list {
    font-size: 2em;
    color: white;
    margin-left: 10x !important;
  }
}
